html{
  min-height: 100vh;
  position: relative;
}
body {
  background-image: url("../assets/images/background.jpg");
  
}
button {
  margin-top: 10px;
}
figure {
  text-align: center;
}
form {
  margin-left: 20%;
}
footer {
  color: white;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
  
}
nav {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.container {
  padding: 20px;
  background-color: #eee;
  margin: 8% 10%;
  width: 60%;
  border-radius: 10px;
}

